<template>
    <div class="notice-page">
        <div>
            <img :src="noticeImg" alt="notice">
        </div>
        <div>
            <p></p>
        </div>
    </div>
</template>
<script>
export default {
    name: 'noticePage',
    data() {
        return {
            noticeImg: require('../../assets/commons/lb@3x.png')
        }
    },
}
</script>
<style lang="">
    .notice-page{
        width: 100%;
        height: 60px;
        background-color: #2A2D36;
    }
    .notice-page div img{
        margin-top: 10px;
        margin-left: 30px;
    }
</style>


