<template>
    <div class="header-page">
        <div class="header-menu">
            <img :src="menuImg" alt="">
        </div>
        <div class="header-logo">
            <img :src="logoImg" alt="">
        </div>
        <div class="header-search">
            <img :src="searchImg" alt="">
        </div>
    </div>
</template>

<script>
export default {
    name: 'headerPage',
    data() {
        return {
            menuImg: require('../../assets/commons/cd@3x.png'),
            logoImg: require('../../assets/commons/logo@3x.png'),
            searchImg: require('../../assets/commons/ss@3x.png')
        }
    },
}
</script>

<style>
    .header-page{
        width:100%;
        height:132px;
        background: #661116;
    }
    .header-page div{
        margin-top: 42px; 
        float: left;
    }
    .header-menu{
        width: 20%;
        text-align: center;
    }
    .header-logo{
        width: 60%;
        text-align: center;
    }
    .header-search{
        width: 20%;
        text-align: center
    }
    @media screen and (max-width: 900px){

    }

</style>


