<template>
    <div class="banner-page">
        <img :src="bannerImg" alt="">
    </div>
</template>
<script>
export default {
    name: 'bannerPage',
    data() {
        return {
            bannerImg: require("../../assets/commons/banner.png"),
        }
    },
}
</script>
<style>
    .banner-page{
        width: 100%;
        height: 400px;
    }
    .banner-page img{
        width: 100%;
    }
</style>


